import React from 'React';
import {StaticQuery, graphql} from 'gatsby';
import Img from 'gatsby-image';

export default function skills () {
    
    return(            
            <StaticQuery
                query={graphql`
                    query skills {
                        allFile (sort: {fields: name}){
                            group(field: relativeDirectory) {
                                edges {
                                    node {
                                        id
                                        name
                                        childImageSharp {
                                            fluid (maxWidth: 150){
                                                ...GatsbyImageSharpFluid
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }                        
                `}

                render={data=> (
                    <section className="skills">
                        <div className="column is-full">
                            <h1>Web Development Skills</h1>
                        </div>
                        <div className="box">
                            <div className="columns is-multiline">
                                {data.allFile.group[4].edges.map( imgs => (
                                    <div id={imgs.node.id} className="column is-desktop is-full-mobile">
                                        <h2>{imgs.node.name}</h2>
                                        <Img 
                                            className="img-skills"
                                            fluid={imgs.node.childImageSharp.fluid}
                                            objectPosition="50% 50%"
                                        />
                                    </div>
                                ))}
                            </div>
                        </div>
                        <div className="column is-full">
                            <h1 className="second-title">Data Analysis/Science Skills</h1>
                        </div>
                        <div className="box">
                            <div className="columns is-multiline">
                                {data.allFile.group[3].edges.map( imgs => (
                                    <div id={imgs.node.id} className="column is-desktop is-full-mobile">
                                        <h2>{imgs.node.name}</h2>
                                        <Img 
                                            className="img-skills"
                                            fluid={imgs.node.childImageSharp.fluid}
                                            objectPosition="50% 50%"
                                        />
                                    </div>
                                ))}
                            </div>
                        </div>
                    </section>
                )}
            /> 
    )
}