import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

const contact = () =>{

    return(
        <StaticQuery
        query= {graphql`
        query contactsQuery {
            allMarkdownRemark(filter: {frontmatter: {link: {ne: null}}}) {
              edges {
                node {
                  frontmatter {
                    link
                    img {
                      childImageSharp {
                        fixed (width: 200) {
                            ...GatsbyImageSharpFixed
                        }
                      }
                    }
                  }
                }
              }
            }
          }
                   
        `}

        render= {data => (
            <section className="contact" id="contact">
                <h1>Don't hesitate to <strong>Contact me</strong></h1>
                <div className="columns is-mobile">
                    {data.allMarkdownRemark.edges.map( contacts => (
                        <a className="column" href={contacts.node.frontmatter.link} target="__blank">
                            <Img fixed={contacts.node.frontmatter.img.childImageSharp.fixed}/>
                        </a>
                    ))}
                </div>
            </section>
        )}
        />
    )
}

export default contact