import React from 'react';
import Skills from '../components/skills';
import '../styles/templates/HomeTemplate.scss';
import Background from '../components/background';
import Contact from '../components/contact';
import { StaticQuery, Link} from 'gatsby';
import Img from 'gatsby-image';


export default function HomeTemplate() {


    return(
        <>
            <Background className="hero" subtitle="I'm a Web Developer" hasButton={true}/>
            <Skills/>
            <section className="portfolio" id="portfolio">
              <div className="columns is-centered">
                  <h1>My Portfolio</h1>
              </div>
              <div className="columns is-multiline">
                <StaticQuery 
                query = {graphql`
                query workQuery {
                    allMarkdownRemark(filter: {frontmatter: {category: {eq: "work"}}}) {
                      nodes {
                        id
                        frontmatter {
                          title
                          description
                          path
                          featureImage {
                            childImageSharp {
                              fixed (height: 350){
                                ...GatsbyImageSharpFixed
                              }
                            }
                          }
                        }
                        html
                      }
                    }
                  }                                   
                `}

                render={data =>(
                    data.allMarkdownRemark.nodes.map( work => (
                                <Link to={work.frontmatter.path} className="column is-4-desktop is-6-tablet is-full-mobile" id={work.id}>
                                    <div className="card">
                                        <div className="card-image">
                                            <Img style={{width: '100%'}} fixed={work.frontmatter.featureImage.childImageSharp.fixed} />
                                        </div>
                                        <div className="card-content">
                                            <h2>{work.frontmatter.title}</h2>
                                            <div className="content">
                                                {work.frontmatter.description}
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                    ))                      
                )}
                />
                </div>
                </section>
                <Contact/>
        </>
    );
}